import React from "react";
import { Container, Row, Col, Button, Input } from "reactstrap";
import LayoutOne from "../../layouts/LayoutOne";
import axiosConfig from "../../axiosConfig";
import swal from "sweetalert";
import Select from "react-select";
import { Country, State, City } from "country-state-city";
import astrologinbg from "../../assets/img/astrologin-bg.jpg";

class UserRequestForm extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      userid: "",
      astroid: "",
      mobile: "",
      firstname: "",
      fullName: "",
      p_firstname: "",
      lastname: "",
      p_lastname: "",
      dob: "",
      p_dob: "",
      date_of_time: "",
      p_date_of_time: "",
      birthPlace: "",
      p_birthPlace: "",
      gender: "",
      marital_status: "",
      occupation: "",
      topic_of_cnsrn: "",
      // entertopic_of_cnsrn: "",
      data: [],
      state: [],
      city: [],
      country: [],
      selectedCountry: null,
      selectedState: null,
      selectedCity: null,
      showpartner: false,
    };
  }
  changeCity = item => {
    this.setState({
      submitPlaceHandler: item,
    });
    axiosConfig
      .post(`/user/geo_detail`, {
        place: item?.name,
      })
      .then(response => {
        this.setState({
          latitude: response?.data?.data?.geonames[0].latitude,
          longitude: response?.data?.data?.geonames[0].longitude,
        });
      })
      .catch(error => {
        console.log(error);
      });
  };

  componentDidMount() {
    let userId = JSON.parse(localStorage.getItem("user_id"));
    axiosConfig
      .get(`/user/viewoneuser/${userId}`)
      .then(response => {
        this.setState({ mobile: response.data.data.mobile });
        this.setState({ firstname: response.data.data.fullname });
      })
      .catch(error => {
        console.log(error);
      });
  }
  changeHandler = e => {
    this.setState({ [e.target.name]: e.target.value });
  };

  submitHandler = e => {
    e.preventDefault();
    let userId = JSON.parse(localStorage.getItem("user_id"));
    let astroId = localStorage.getItem("astroId");
    const Year = new Date(this.state.dob).getFullYear();
    const Month = new Date(this.state.dob).getMonth();
    const Day = new Date(this.state.dob).getDay();
    const arr = this.state.date_of_time.split(":");
    const hour = parseInt(arr[0]);
    const min = parseInt(arr[1]);
    let obj = {
      userid: userId,
      astroid: astroId,
      mobile: parseInt(this.state.mobile),
      firstname: this.state.firstname,
      // lastname: "nkk",
      p_firstname: this.state.p_firstname,
      p_lastname: this.state.p_lastname,
      dob: this.state.dob,
      p_dob: this.state.p_dob,
      birthPlace: this.state.birthPlace,
      p_birthPlace: this.state.p_birthPlace,
      date_of_time: this.state.date_of_time,
      p_date_of_time: this.state.p_date_of_time,
      gender: this.state.gender,
      marital_status: this.state.marital_status,
      occupation: this.state.occupation,
      topic_of_cnsrn: this.state.topic_of_cnsrn,
      type: "Chat",
      // entertopic_of_cnsrn: this.state.entertopic_of_cnsrn,
    };
    const birthDetails = {
      userid: userId,
      astroid: astroId,
      day: Day,
      month: Month,
      year: Year,
      hour: hour,
      min: min,
      lat: this.state.latitude,
      lon: this.state.longitude,
      tzone: 5.5,
    };

    // axiosConfig
    //   .post(`/user/add_chat_intake`, obj)
    //   .then(response => {
    //     console.log(response.data.data);
    //     swal("Success!", "Submitted SuccessFully!", "success");
    //     this.props.history.push("/chatApp");
    //   })
    //   .catch(error => {
    //     swal("Error!", "You clicked the button!", "error");
    //   });
    axiosConfig
      .post(`/user/birth_details`, birthDetails)
      .then(response => {})
      .catch(error => {
        swal("Error!", "You clicked the button!", "error");
      });
    if (userId !== "" && userId !== null) {
      axiosConfig
        .post(`/user/add_chat_intake`, obj)

        .then(response => {
          // const data = {
          //   userid: userId,
          //   astroid: astroId,
          //   type: "Chat",
          // };
          // axiosConfig
          //   .post(`/user/addCallWallet`, data)
          //   .then(res => {
          //     sessionStorage.setItem("notificationdata", res.data?._id);
          //     if (res.data.status === true) {
          //       this.props.history.push({
          //         pathname: "/waitingpagechat",
          //         state: res.data,
          //       });
          //     } else swal("Not having Enough Balance");
          //   })
          //   .catch(err => {
          //     console.log(err);
          //   });
        })
        .catch(error => {
          swal("Error!", "Error Occurred!", "error");
          console.log(error);
        });
    } else {
      swal("Need to Login first");
    }
  };
  render() {
    return (
      <LayoutOne headerTop="visible">
        <section className="pt-0 pb-0">
          <div
            className=""
            style={{
              float: "left",
              width: "100%",
              backgroundColor: "#272727",
              position: "relative",
              backgroundAttachment: "fixed",
              backgroundSize: "cover",
              color: "#ffffff",
              padding: " 50px 0px 50px 0px",
              backgroundImage: `url(${astrologinbg})`,
              backgroundPosition: "center center",
              backgroundRepeat: " no-repeat",
              textAlign: "center",
            }}
          >
            <Container>
              <Row>
                <Col md="12">
                  <div className="leftcont text-left">
                    <h1>Chat InTake Form</h1>
                  </div>
                </Col>
              </Row>
            </Container>
          </div>
        </section>
        <section className="">
          <Container>
            <Row>
              <Col lg="12">
                <div className="wal-amt">
                  <h3>Chat InTake Form </h3>
                  <hr></hr>

                  <div className="d-flex">
                    <Row>
                      <Col lg="2" sm="2" md="2">
                        <input
                          onClick={e => {
                            this.setState({ showpartner: e.target.checked });
                          }}
                          width={14}
                          type="checkbox"
                          id="box"
                          name="box"
                          value="intakeform"
                        />
                      </Col>
                      <Col>
                        <span className="mt-2">
                          Want to Add Partner Details
                        </span>
                      </Col>
                    </Row>
                  </div>
                  <form onSubmit={e => this.submitHandler(e)}>
                    <Row>
                      <Col md="4">
                        <div class="form-group mtb-10">
                          <label>Mobile Number*</label>
                          <input
                            type="number"
                            name="mobile"
                            value={this.state.mobile}
                            onChange={this.changeHandler}
                            required
                            placeholder="Enter Your Number"
                          />
                        </div>
                      </Col>
                      <Col md="4">
                        <div class="form-group mtb-10">
                          <label>FullName*</label>
                          <input
                            type="text"
                            name="firstname"
                            required
                            placeholder="Enter Your FirstName"
                            value={this.state.firstname}
                            onChange={this.changeHandler}
                          />
                        </div>
                      </Col>

                      {/* <Col md="4">
                        <div class="form-group mtb-10">
                          <label> Last Name*</label>
                          <input
                            required
                            type="text"
                            name="lastname"
                            placeholder="Enter Your Lastname"
                            value={this.state.lastname}
                            onChange={this.changeHandler}
                          />
                        </div>
                      </Col> */}
                      <Col md="4">
                        <div class="form-group mtb-10">
                          <label>Date of Birth*</label>
                          <input
                            type="date"
                            name="dob"
                            value={this.state.dob}
                            onChange={this.changeHandler}
                            required
                            placeholder="Enter Your Number"
                          />
                        </div>
                      </Col>
                      <Col md="4">
                        <div class="form-group mtb-10">
                          <label>Time of Birth*</label>
                          <input
                            type="time"
                            required
                            name="date_of_time"
                            value={this.state.date_of_time}
                            onChange={this.changeHandler}
                            id="timeinput"
                          />
                        </div>
                      </Col>
                      <Col md="4">
                        <div class="form-group mtb-10">
                          <label>Birth Place*</label>
                          <input
                            type="text"
                            name="birthPlace"
                            value={this.state.birthPlace}
                            onChange={this.changeHandler}
                            required
                            placeholder="Enter Your  Birth Place"
                          />
                        </div>
                      </Col>

                      {this.state.showpartner ? (
                        <>
                          <Col md="4">
                            <div class="form-group mtb-10">
                              <label>Partner First Name</label>
                              <input
                                type="text"
                                name="p_firstname"
                                placeholder="Enter Your Patner FirstName"
                                value={this.state.p_firstname}
                                onChange={this.changeHandler}
                              />
                            </div>
                          </Col>
                          <Col md="4">
                            <div class="form-group mtb-10">
                              <label>Partner Last Name</label>
                              <input
                                type="text"
                                name="p_lastname"
                                placeholder="Enter Your Patner LastName"
                                value={this.state.p_lastname}
                                onChange={this.changeHandler}
                              />
                            </div>
                          </Col>

                          <Col md="4">
                            <div class="form-group mtb-10">
                              <label> Partner Date of Birth</label>
                              <input
                                type="date"
                                name="p_dob"
                                value={this.state.p_dob}
                                onChange={this.changeHandler}
                                placeholder="Enter Your Number"
                              />
                            </div>
                          </Col>

                          <Col md="4">
                            <div class="form-group mtb-10">
                              <label> Partner Time of Birth</label>
                              <input
                                type="time"
                                name="p_date_of_time"
                                value={this.state.p_date_of_time}
                                onChange={this.changeHandler}
                              />
                            </div>
                          </Col>

                          <Col md="4">
                            <div class="form-group mtb-10">
                              <label> Partner Birth Place</label>
                              <input
                                type="text"
                                name="p_birthPlace"
                                value={this.state.p_birthPlace}
                                onChange={this.changeHandler}
                                placeholder="Enter Your  Birth Place"
                              />
                            </div>
                          </Col>
                        </>
                      ) : null}
                      <Col lg="4" md="4" sm="6" className="mt-2">
                        <label>Country</label>
                        <Select
                          options={Country.getAllCountries()}
                          getOptionLabel={options => {
                            return options["name"];
                          }}
                          getOptionValue={options => {
                            return options["name"];
                          }}
                          value={this.state.selectedCountry}
                          onChange={item => {
                            this.setState({ selectedCountry: item });
                            axiosConfig
                              .post(`/user/time_zone`, {
                                country_code: item?.timezones[0].zoneName,
                              })
                              .then(response => {
                                this.setState({
                                  timezone: response?.data?.data?.timezone,
                                });
                              })
                              .catch(error => {
                                console.log(error);
                              });
                          }}
                        />
                      </Col>

                      <Col lg="4" md="4" sm="6" className="mt-2">
                        <label>State</label>
                        <Select
                          options={State?.getStatesOfCountry(
                            this.state.selectedCountry?.isoCode
                          )}
                          getOptionLabel={options => {
                            return options["name"];
                          }}
                          getOptionValue={options => {
                            return options["name"];
                          }}
                          value={this.state.selectedState}
                          onChange={item => {
                            this.setState({ selectedState: item });
                          }}
                        />
                      </Col>

                      <Col lg="4" md="4" sm="6" className="mt-2">
                        <label>City</label>
                        <Select
                          options={City.getCitiesOfState(
                            this.state.selectedState?.countryCode,
                            this.state.selectedState?.isoCode
                          )}
                          getOptionLabel={options => {
                            return options["name"];
                          }}
                          getOptionValue={options => {
                            return options["name"];
                          }}
                          value={this.state.submitPlaceHandler}
                          onChange={item => {
                            this.changeCity(item);
                          }}
                        />
                      </Col>

                      <Col lg="4" md="4" sm="6" className="my-2 ">
                        <label>Gender*</label>
                        <Input
                          id="exampleSelect"
                          type="select"
                          name="gender"
                          required
                          value={this.state.data.gender}
                          onChange={this.changeHandler}
                        >
                          <option>Select Gender</option>
                          <option>Male</option>
                          <option>Female</option>
                        </Input>
                      </Col>

                      <Col lg="4" md="4" sm="6" className="my-2 ">
                        <label>Marital Status*</label>
                        <Input
                          type="select"
                          name="marital_status"
                          required
                          value={this.state.marital_status}
                          onChange={this.changeHandler}
                        >
                          <option>Select Marital Status</option>
                          <option>Single</option>
                          <option>Married</option>
                          <option>Divorced</option>
                          <option>Separated</option>
                          <option>Widowed</option>
                        </Input>
                      </Col>
                      <Col lg="4" md="4" sm="6">
                        <div class="form-group mtb-10">
                          <label>Occupation*</label>
                          <Input
                            type="select"
                            required
                            name="occupation"
                            value={this.state.data.occupation}
                            onChange={this.changeHandler}
                          >
                            <option>Select Employed in</option>
                            <option>Private Sector</option>
                            <option>Govt Sector</option>
                            <option>Business/Self Employed</option>
                            <option>Civil Services</option>
                            <option>Defence</option>
                            <option>Not Working</option>
                            <option>Student</option>
                          </Input>
                        </div>
                      </Col>
                      <Col lg="4" md="4" sm="6">
                        <div class="form-group mtb-10">
                          <label>Topic of concern*</label>
                          <Input
                            type="select"
                            name="topic_of_cnsrn"
                            required
                            value={this.state.data.topic_of_cnsrn}
                            onChange={this.changeHandler}
                          >
                            <option>Select Topic</option>
                            <option>Career and Business</option>
                            <option>Marriage</option>
                            <option>Love and Relationship</option>
                            <option>Wealth and Property</option>
                            <option>Education</option>
                            <option>Legal Matters</option>
                            <option>Child Name Consultation</option>
                            <option>Business Name Consultation</option>
                            <option>Gem Stone Consultation</option>
                            <option>Commodity trading consultation</option>
                            <option>Match making</option>
                            <option>Birth Time Rectification</option>
                            <option>Name Correction Consultation</option>
                            <option>Travel Abroad Consulation</option>
                            <option>Remedy Consultation</option>
                            <option>Health Consultation</option>
                            <option>Others</option>
                          </Input>
                        </div>
                      </Col>
                      {/* <Col md="4">
                        <div class="form-group mtb-10">
                          <label>Enter topic of concern:</label>
                          <input
                            type="text"
                            name="entertopic_of_cnsrn"
                            required
                            placeholder="Enter Topic Of Concern "
                            value={this.state.entertopic_of_cnsrn}
                            onChange={this.changeHandler}
                          />
                        </div>
                      </Col> */}
                      <Col md="12" className="mt-3">
                        <Button className="btn btn-warning">
                          Start chat with {localStorage.getItem("astroname")}
                        </Button>
                      </Col>
                    </Row>
                  </form>
                </div>
              </Col>
            </Row>
          </Container>
        </section>
      </LayoutOne>
    );
  }
}
export default UserRequestForm;
